		desc: "从阿里方面了解到，阿里无人店项目团队已确认被纳入“天猫新零售战略大图”，并已于月前进驻“新零售技术实验室”，专心攻克、研发能应用于更大客流量、更复杂商业场景的无人零售难题",
		content: [{ type: "text", detail: "作为全球范围无人零售“第一个吃螃蟹的人”，阿里巴巴无人店自淘宝造物节惊艳亮相后，便引来无数关注与热议。" }, { type: "text", detail: "据了解，该项目团队已确认被纳入“天猫新零售战略大图”中，并已于月前低调进驻“新零售技术实验室”，专心攻克、研发能应用于更大客流量、更复杂商业场景的无人零售难题" }, { type: "img", detail: __webpack_require__("tmZ/") }, { type: "text", detail: "他表示，“计划今年年底完成技术升级，并在杭州落成全球第一家真正意义上的无人零售实体店，向广大消费者长期开放。” 而截至目前，选址信息还处于严格保密阶段" }, { type: "img", detail: __webpack_require__("NfOw") }, { type: "text", detail: "1个月前，当阿里推出“无人零售”快闪店后，关于“无人零售会不会提升失业率”的讨论盛嚣尘上。事实上，纯机械化的工作被机器取代已然成为趋势。在英国，像是Tesco、Sainsbury、ASDA这样的超市中，自助结算机已成为标配。" }, { type: "text", detail: "应宏表示，那些有关“无人零售将造成大面积失业”的言论纯属无稽之谈。他举例子说，“80年代，公交车上还有售票员，但后面出现的公交卡并没有造成交通系统大多数人失业。所以完全没有理由去惧怕科技的发展，科技极大的方便了我们的生活。”" }, { type: "text", detail: "“不仅不会降低就业率，反而将催生更多新工种。”应宏表示，以无人零售店为例，超市收银员这样机械化的前台工种确实可以被替换，但这并不意味着无人零售将会消灭工作，在他看来，这是催生新工种、推动进步的必经之路。“如智能物流管理员、智能仓库拣货员、数据分析师等等，这些更可持续优化的新工种将成为新零售时代下最紧缺的岗位。而换句话说，无人零售店虽然取代了收银员等前台机械岗位，但却催生了更多智能化的后台岗位，更有意思的是，在技术足够成熟的情况下，未来店员甚至可以在家上班。" }]
